import React from 'react';
import renderHTML from 'react-render-html';
import { Link } from 'react-router-dom';
import { leveningStr } from './helper';

export default ({post, handleBookmark, handleRemoveBookmark}) => {
  const { id, title, image, body, bookmark } = post;
  return (
    <div className="post">
      <h3><Link to={`/post/${id}`}>{title}</Link></h3>
      {/* eslint-disable-next-line */}
      <img src = {image} />
      <p>{renderHTML(leveningStr(body, 250))}</p>
      <ul>
        <li><Link to={`/post/${id}`} className="btn btn-more">c thm...</Link></li>
        <li>{bookmark ? <button className="btn btn-remove-bookmarks" onClick={() => handleRemoveBookmark(post)} > nh du</button> : <button className="btn btn-bookmarks" onClick={() => handleBookmark(post)} >Thm vo nh du</button>}</li>
      </ul>
    </div>
  )
}
